import styled from "styled-components";
import MyHeader from "../components/MyHeader";
import { useUserState } from "../context/UserContext";

const HomeImgStyled = styled.div`
  display: flex;
  & > img {
    height: 320px;
  }
`;

const HomeTextStyled = styled.div`
  width: 500px;
  font-size: 21px;
  margin-right: 50px;
  & > div {
    &:first-child {
      color: #6247aa;
      font-size: 60px;
      font-family: "Binggrae-Two";
    }
    &:nth-child(2) {
      color: #6247aa;
      font-size: 25px;
      margin-bottom: 50px;
      font-weight: 600;
    }
    &:nth-child(3) {
      width: 470px;
      margin-bottom: 15px;
    }
  }
`;

const Home = () => {
  const { user } = useUserState();
  return (
    <div>
      <MyHeader />
      <div className="home_all">
        <HomeTextStyled>
          <div>alletty</div>
          <div> ai      </div>
          <div>
             ai  <b>MonneyBot</b>   {" "}
            <b>  </b>   <b></b> !
          </div>
          <div>
             <b></b>      :)
          </div>
        </HomeTextStyled>
        <HomeImgStyled>
          <img src="/assets/chatbot_mom.png" />
          <img src="/assets/character_org.png" />
        </HomeImgStyled>
      </div>
    </div>
  );
};

export default Home;
