import { useState } from "react";
import styled from "styled-components";

const RankingItemStyled = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 370px;
  padding-right: 20px;
  padding-top: 5px;
  margin: 20px 20px;
  border-radius: 20px;
  background-color: white;
  box-shadow: 1px 1px 3px #cccccc;
`;

const RankingItemImgStyled = styled.div`
  & img {
    height: 180px;
  }
`;

const RankingItemInfoStyled = styled.div`
  font-size: 18px;
  :first-child {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 5px;
  }
`;

const RankingTopList = ({ rankingTopList }) => {
  console.log(rankingTopList);
  return (
    <div className="RankingTopList">
      <div>  </div>
      <div className="rank_weekly_box">
        <RankingItemStyled>
          <RankingItemImgStyled>
            <img src={`/assets/character_${rankingTopList[0].position}.png`} />
          </RankingItemImgStyled>
          <RankingItemInfoStyled>
            <div>  1</div>
            <div>{rankingTopList[0].memberId} </div>
            <div>
               {rankingTopList[0].level}, {rankingTopList[0].position}
            </div>
            <div>  {rankingTopList[0].totalSavingMoney}  !</div>
          </RankingItemInfoStyled>
        </RankingItemStyled>

        <RankingItemStyled>
          <RankingItemImgStyled>
            <img src={`/assets/character_${rankingTopList[1].position}.png`} />
          </RankingItemImgStyled>
          <RankingItemInfoStyled>
            <div>  2</div>
            <div>{rankingTopList[1].memberId} </div>
            <div>
               {rankingTopList[1].level}, {rankingTopList[1].position}
            </div>
            <div>  {rankingTopList[1].totalSavingMoney}  !</div>
          </RankingItemInfoStyled>
        </RankingItemStyled>

        <RankingItemStyled>
          <RankingItemImgStyled>
            <img src={`/assets/character_${rankingTopList[2].position}.png`} />
          </RankingItemImgStyled>
          <RankingItemInfoStyled>
            <div>  3</div>
            <div>{rankingTopList[2].memberId} </div>
            <div>
               {rankingTopList[2].level}, {rankingTopList[2].position}
            </div>
            <div>  {rankingTopList[2].totalSavingMoney}  !</div>
          </RankingItemInfoStyled>
        </RankingItemStyled>
      </div>
    </div>
  );
};

export default RankingTopList;
